<template>
  <div class="bookDetail" v-if="book">
    <!-- 头部 -->
    <BookDetailHeader :title="book.title" />
    <!-- 书籍详情 -->
    <div class="container">
      <div class="book-info">
        <van-card
          :desc="book.author"
          :title="book.title"
          :thumb="`http://statics.zhuishushenqi.com${book.cover}`"
          v-if="book"
        >
          <template #tags>
            <div class="mb" v-if="book.rating">
              <van-rate
                v-model="starNum"
                :size="12"
                color="#ffd21e"
                void-icon="star"
                void-color="#eee"
                readonly
              />
              <span>{{ book.rating.score }}分</span>
              <span>/</span>
              <span>{{ book.rating.tip }}</span>
            </div>
            <div class="mb">
              <span>{{ book.majorCate }}</span>
              <span v-if="book.minorCate">/</span>
              <span>{{ book.minorCate }}</span>
            </div>
            <div class="mb">
              <span>{{ book.wordCount | funsFilter }}</span>
              <!-- 过滤 -->
              <span>|</span>
              <span>{{ book.isSerial ? "连载" : "完结" }}</span>
              <span v-if="book.isSerial"
                >({{ book.updated | dateChange }}前更新)</span
              >
            </div>
          </template>
        </van-card>
      </div>
      <!-- 阅读按钮 -->
      <div class="read-link">
        <template>
          <van-button
            plain
            hairline
            color="#000000"
            @click="delBookrack(id)"
            v-if="
              myBookRack.find((item) => {
                return item.id === id;
              })
            "
            >移出书架</van-button
          >
          <van-button
            plain
            hairline
            type="danger"
            @click="addInBookrack(id)"
            v-else
            >加入书架</van-button
          >
        </template>
        <van-button type="danger" @click="toRead">开始阅读</van-button>
      </div>
    </div>
    <!-- 书籍支持率以及日更字数 -->
    <div class="reader-data">
      <p class="reader-data-item">
        <span class="key">追人气</span>
        <span class="itemData">{{ book.latelyFollower | funsFilter }}</span>
      </p>
      <p class="reader-data-item">
        <span class="key">读者留存率</span>
        <span class="itemData">{{ book.retentionRatio }}%</span>
      </p>
      <p class="reader-data-item">
        <span class="key">日更字数/天</span>
        <span class="itemData">{{ book.serializeWordCount }}</span>
      </p>
    </div>
    <!-- 简介与目录 -->
    <div class="introduction">
      <!-- 简介 -->
      <div
        class="introduction-top"
        @click="hide = !hide"
        :class="{ hide: hide }"
      >
        {{ book.longIntro }}
        <i :class="{ iToggle: !hide }"></i>
      </div>
      <div class="ad-item"></div>
      <!-- 目录 -->
      <div
        class="menu"
        @click="
          $router.push({
            path: '/bookDetail/' + id + '/chapters',
            query: {
              bookname: book.title,
            },
          })
        "
      >
        <span>目录</span>
        <p>
          {{ book.lastChapter }}
          <i></i>
        </p>
      </div>
    </div>
    <!-- 评论 -->
    <div class="hot-comment-top">
      <h2>热门书评</h2>
      <van-loading
        type="spinner"
        size="24px"
        v-if="commentLoading && comments.length == 0"
      >
        加载中...
      </van-loading>
      <ol v-else>
        <li v-for="(comment, index) in comments" :key="index">
          <CommentItem :comment="comment" />
        </li>
      </ol>
      <p class="noneComment" v-if="comments.length === 0 && !commentLoading">
        暂无评论
      </p>
    </div>
    <!-- 更多 -->
    <div class="more">
      <Title title="更多相关" :loading="commentLoading" :typeBooks="moreBooks"
        >更多相关</Title
      >
      <van-loading type="spinner" size="24px" v-if="commentLoading">
        加载中...
      </van-loading>
      <template v-else>
        <ul class="ul-type-one" v-if="moreBooks">
          <BookItemTypeOne
            v-for="item in moreBooks.slice(0, 4)"
            :item="item"
            :key="item._id"
            :loading="commentLoading"
          />
        </ul>
      </template>
    </div>
    <!-- 回到顶部 -->
    <BackToTop />
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import BackToTop from "@/components/BackToTop.vue";
import CommentItem from "@/components/CommentItem.vue";
import Title from "@/components/Title.vue";
import BookItemTypeOne from "@/components/BookItemTypeOne.vue";
import BookDetailHeader from "@/components/BookDetailHeader.vue";
import { Toast } from "vant";
export default {
  components: {
    BackToTop,
    CommentItem,
    Title,
    BookItemTypeOne,
    BookDetailHeader,
  },
  data() {
    return {
      id: this.$route.params.id,
      // id: this.$route.query.id,
      book: null,
      starNum: 0,
      hide: true,
      comments: [],
      commentLoading: true,
      commentNum: 0, //优化的计算值
      moreBooks: [],
      timer: null,
    };
  },
  computed: mapState(["myBookRack"]),
  watch: {
    "$route.params.id"(n) {
      console.log("路由id刷新了", n);
      this.id = n;
      document.documentElement.scrollTop = 0;
      if (n) {
        this.detailSearch();
      }
    },
    commentNum(n) {
      if (n >= 3) {
        this.commentLoading = false;
        this.commentNum = 0;
        this.$root.isShowLoading = false;
      }
    },
  },
  filters: {
    funsFilter(v) {
      return (v / 10000).toFixed(2) + "万";
    },
    dateChange(t) {
      t = t.replace(/T/, " ");
      t = t.split(".")[0];
      var hour = new Date().getTime() - new Date(t).getTime();
      hour = parseInt(hour / 1000 / 60 / 60);
      if (hour < 24) {
        return hour + "小时";
      }
      var day = hour / 24;
      if (day > 365) {
        var year = parseInt(day / 365);
        return year + "年" + parseInt(day - year * 365) + "天";
      } else {
        return parseInt(day) + "天";
      }
    },
  },
  created() {
    // console.log("当前书籍详情id", this.id);
    // console.log(this.$root.isShowLoading);
    // 书籍详情查询
    this.detailSearch();
  },
  methods: {
    ...mapMutations(["addInBookrack", "delBookrack", "getAllChapter"]),
    detailSearch() {
      this.commentLoading = true;
      this.$root.isShowLoading = true;
      // 书本详情
      this.axios
        .get("/book-info/" + this.id)
        .then((res) => {
          // console.log(res.data);
          this.book = res.data;
          this.starNum = res.data.safelevel;
        })
        .finally(() => {
          this.commentNum++;
        });
      // 评价
      this.axios
        .get("book/reviews?book=" + this.id)
        .then((res) => {
          // 获取觉得有用的评价属性中值比较高的评论
          // console.log(res);
          this.comments = res.data.reviews
            .slice(0, 60)
            .sort((a, b) => {
              return b.helpful.yes - a.helpful.yes;
            })
            .slice(0, 10);
        })
        .finally(() => {
          this.commentNum++;
        });
      // 推荐
      this.axios
        .get("/recommend/" + this.id)
        .then((res) => {
          // console.log(res.data.books);
          this.moreBooks = res.data.books;
        })
        .finally(() => {
          this.commentNum++;
        });
    },
    toRead() {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      Toast("正在获取，请稍后 Thanks♪(･ω･)ﾉ");
      this.timer = setTimeout(() => {
        this.$router.push({
          path: "/bookDetail/" + this.id + "/read",
          query: { bookName: this.book.title },
        });
      }, 1000);
    },
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === "Read" || to.name === "Chapters") {
      console.log("前往阅读，获取章节");
      // 判断本地存储是否已经有这个书本的全部章节,如果有直接跳转
      if (localStorage.getItem("allChapters")) {
        console.log(JSON.parse(localStorage.getItem("allChapters")).id);
        if (JSON.parse(localStorage.getItem("allChapters")).id === this.id) {
          console.log("章节已获取过");
          next();
          return;
        } else {
          console.log("章节正在获取");
        }
      }

      this.axios
        .get("/book-sources?view=summary&book=" + this.id)
        .then((res) => {
          this.axios.get("/book-chapters/" + res.data[0]._id).then((res) => {
            // console.log("全部章节数据", res);
            // 查找是否有这个书，获取章节下标
            let bookIndex = this.myBookRack.findIndex((item) => {
              return item.id === this.id;
            });

            localStorage.setItem(
              "allChapters",
              JSON.stringify({
                chapters: res.data.chapters,
                index: bookIndex > -1 ? this.myBookRack[bookIndex].index : 0,
                id: this.id,
                // pageNum:
                // bookIndex > -1 ? this.myBookRack[bookIndex].pageNum : 0, //页码
              })
            );
            this.getAllChapter({
              chapters: res.data.chapters,
              index: bookIndex > -1 ? this.myBookRack[bookIndex].index : 0,
              // pageNum: bookIndex > -1 ? this.myBookRack[bookIndex].pageNum : 0, //页码
            });
            next();
          });
        });
    } else {
      next();
    }
  },
};
</script>

<style lang="less">
.bookDetail {
  margin: 50px 0 30px;
  // margin-bottom: 30px;

  .container {
    padding: 0 10px 0;
    box-sizing: border-box;
    .book-info {
      margin-bottom: 10px;
      .van-card {
        background-color: #fff;
        padding: 8px;
        .van-card__thumb {
          .van-image {
            img {
              width: 84px;
              height: 112px;
            }
          }
        }
        .van-card__content {
          padding-top: 3px;
          .van-card__title,
          .van-card__desc,
          .mb {
            margin-bottom: 5px;
            .van-rate {
              margin-right: 10px;
            }
          }
          .van-card__title {
            font-size: 15px;
            font-weight: 600;
          }
          span {
            margin-right: 3px;
          }
        }
      }
    }
    .read-link {
      display: flex;
      justify-content: space-around;
      padding-bottom: 30px;
      .van-button {
        height: 30px;
      }
    }
  }
  .reader-data {
    display: flex;
    padding: 20px 0;
    border-top: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    .reader-data-item {
      flex: 1;
      text-align: center;
      .key {
        font-weight: 400;
        color: #999;
        font-size: 12px;
      }
      .itemData {
        display: block;
        margin-top: 3px;
        color: #222;
        font-weight: 400;
      }
    }
  }
  .introduction {
    padding: 20px 0 0 20px;
    border-bottom: 1px solid #ebebeb;
    .introduction-top {
      padding-right: 20px;
      color: #222;
      font-size: 13px;
      line-height: 22.5px;
      position: relative;
      > i {
        z-index: 3;
        background: url("../../public/img/downArrowhead.svg") no-repeat;
        background-size: 100%;
        width: 15px;
        height: 15px;
        position: absolute;
        right: 10px;
        bottom: 0;
        &.iToggle {
          background: url("../../public/img/upArrowhead.svg") no-repeat;
          background-size: 100%;
        }
      }
    }
    .ad-item {
      width: 100%;
      height: 1px;
      background: #ebebeb;
      font-weight: 400;
      box-sizing: border-box;
      margin: 10px 0;
    }
    .hide {
      // height: 70px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .menu {
      display: flex;
      justify-content: space-between;
      padding: 10px 10px 20px 0;
      > span {
        font-size: 14px;
        color: #333;
      }
      > p {
        font-size: 12px;
        max-width: 60%;
        color: #999;
        word-break: break-all;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        > i {
          background: url("../../public/img/rightArrowhead.svg") no-repeat;
          background-size: 14px 14px;
          width: 14px;
          height: 14px;
          display: inline-block;
          vertical-align: middle;
          margin-left: 5px;
        }
      }
    }
  }
  .hot-comment-top {
    h2 {
      padding: 3px 0 3px 20px;
      font-size: 14px;
      font-weight: 400;
    }
    .van-loading {
      width: 100px;
      margin: 10px auto;
      color: #e4011b;
      .van-loading__text {
        color: #e4011b;
      }
    }
    .noneComment {
      text-align: center;
      padding: 10px 0;
      font-size: 12px;
      color: #999;
    }
  }
  .more {
    padding: 0 10px 20px;
    box-sizing: border-box;
    .van-loading {
      width: 100px;
      margin: 10px auto;
      color: #e4011b;
      .van-loading__text {
        color: #e4011b;
      }
    }
    .ul-type-one {
      display: flex;
      margin-top: 5px;
    }
  }
}
</style>