<template>
  <div class="commentItem">
    <van-card
      :title="comment.author.nickname"
      :desc="comment.title"
      :thumb="`http://statics.zhuishushenqi.com${comment.author.avatar}`"
    >
      <template #tags>
        <van-rate
          :value="comment.rating"
          :size="10"
          color="gold"
          void-icon="star"
          void-color="#eee"
        />
        <p :class="{ 'van-article': hide }" @click="hide = !hide">
          {{ comment.content }}
        </p>
        <p class="love">
          <span> {{ comment.updated | dateChange }}前 </span>
          <span>{{ comment.helpful.yes }}人觉得有用</span>
        </p>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  props: ["comment"],
  data() {
    return {
      hide: true,
    };
  },
  filters: {
    dateChange(t) {
      t = t.replace(/T/, " ");
      t = t.split(".")[0];
      var hour = new Date().getTime() - new Date(t).getTime();
      hour = parseInt(hour / 1000 / 60 / 60);
      if (hour < 24) {
        return hour + "小时";
      }
      var day = hour / 24;
      if (day > 365) {
        var year = parseInt(day / 365);
        return year + "年" + parseInt(day - year * 365) + "天";
      } else {
        return parseInt(day) + "天";
      }
    },
  },
};
</script>
<style lang="less">
.commentItem {
  .van-card {
    background: #fff;
    .van-card__thumb {
      width: 33px;
      height: 33px;
      .van-image {
        img {
          width: 33px;
          height: 33px;
          border-radius: 50%;
        }
      }
    }
    .van-card__content {
      padding-bottom: 10px;
      border-bottom: 1px solid #ebebeb;
      .van-card__title {
        color: #a58d5e;
        font-size: 12px;
      }
      .van-card__desc {
        font-weight: 700;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        color: #000;
        height: 20px;
        line-height: 20px;
      }
      p {
        color: #999;
        line-height: 18px;
      }
      .van-article {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .love {
        display: flex;
        padding-top: 10px;
        color: #999;
        justify-content: space-between;
      }
    }
  }
}
</style>